#header {
  background-image: url(../images/bg-header.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  width: 100%;
  height: 100vh;
  padding: 20px 0 20px 0; }

.logo {
  cursor: pointer; }

.menu-item {
  margin-left: 40px;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 600;
  font-size: 16px;
  display: inline-block;
  cursor: pointer; }
  .menu-item:hover {
    color: #ffde00; }

.header-nav {
  margin: 20px 0 0 0;
  float: right; }
  .header-nav > .menu-item:first-child {
    margin: 0; }

.burger-menu {
  display: none;
  float: right;
  height: 30px;
  width: 50px;
  position: relative;
  margin-top: 20px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  cursor: pointer; }
  .burger-menu > span {
    width: 100%;
    height: 6px;
    background-color: white;
    left: 0;
    border-radius: 5px;
    -webkit-transition: top 0.2s 0.2s, bottom 0.2s 0.2s, opacity 0.2s 0.2s, -webkit-transform 0.2s 0.4s;
    transition: top 0.2s 0.2s, bottom 0.2s 0.2s, opacity 0.2s 0.2s, -webkit-transform 0.2s 0.4s;
    transition: top 0.2s 0.2s, bottom 0.2s 0.2s, opacity 0.2s 0.2s, transform 0.2s 0.4s;
    transition: top 0.2s 0.2s, bottom 0.2s 0.2s, opacity 0.2s 0.2s, transform 0.2s 0.4s, -webkit-transform 0.2s 0.4s; }
  .burger-menu .burger-1 {
    position: absolute;
    top: 0; }
  .burger-menu .burger-2 {
    position: absolute;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%); }
  .burger-menu .burger-3 {
    position: absolute;
    bottom: 0; }
  .burger-menu.active {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg); }
    .burger-menu.active .burger-1 {
      top: calc(50% - 3px);
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg); }
    .burger-menu.active .burger-2 {
      top: 50%;
      -webkit-transform: translate(0, -50%);
              transform: translate(0, -50%);
      opacity: 0; }
    .burger-menu.active .burger-3 {
      bottom: calc(50% - 3px);
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg); }

.bottom-header {
  padding-top: 200px;
  text-align: center;
  color: white; }
  .bottom-header h1 {
    font-size: 80px;
    font-weight: 1000;
    display: inline-block;
    margin: 0 5px 5px 5px; }
  .bottom-header .heart {
    color: #ffde00;
    font-size: 80px;
    display: inline-block;
    letter-spacing: -5px;
    font-weight: 1000;
    opacity: 0;
    -webkit-animation: show-heart 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94) 2.6s forwards;
            animation: show-heart 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94) 2.6s forwards; }

@-webkit-keyframes show-heart {
  0% {
    opacity: 0;
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom; }
  100% {
    opacity: 1;
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
    -webkit-transform-origin: bottom;
            transform-origin: bottom; } }

@keyframes show-heart {
  0% {
    opacity: 0;
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom; }
  100% {
    opacity: 1;
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
    -webkit-transform-origin: bottom;
            transform-origin: bottom; } }
  .bottom-header .header-description {
    font-weight: 600;
    margin: 10px; }

.big-header-button {
  background-color: rgba(0, 0, 0, 0.2);
  border: 4px solid white;
  border-radius: 3px;
  height: 70px;
  padding: 0 50px;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin: 20px;
  font-weight: 700;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  font-size: 18px; }
  .big-header-button:hover {
    background-color: rgba(255, 222, 0, 0.2); }

/*<---------------Mobile Menu----------------->*/
.mobile-menu {
  width: 100%;
  text-align: center;
  font-weight: 500;
  margin-top: 10px;
  -webkit-transition: 1s;
  transition: 1s;
  position: absolute;
  z-index: 1;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  -webkit-transform-origin: 100% 0%;
          transform-origin: 100% 0%; }
  .mobile-menu > ul {
    padding: 0; }
  .mobile-menu.menu-opened {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
    background: white;
    -webkit-transition: 1s;
    transition: 1s; }

.mobile-menu-item {
  list-style-type: none;
  border-bottom: 1.5px solid gray; }
  .mobile-menu-item > p {
    margin: 0;
    padding: 15px;
    cursor: pointer; }

@-webkit-keyframes mobile-menu-toggle {
  0% {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%; }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%; } }

@keyframes mobile-menu-toggle {
  0% {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%; }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%; } }

@media screen and (max-width: 1000px) {
  .header-nav {
    display: none; }
  .burger-menu {
    display: block; }
  #header {
    background-attachment: scroll;
    background-attachment: initial;
    height: 850px; } }

@media screen and (max-width: 991px) {
  .header-column {
    height: 221px; } }

@media screen and (max-width: 767px) {
  .header-column {
    height: 317px; } }
