.team {
  height: 100%;
  width: 100%;
  position: relative; }
  .team::before {
    content: "";
    background-image: url(../images/pattern.bg.png);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -101; }

.team-content-animation {
  animation: show-more-team 0.5s linear reverse both; }

@keyframes show-more-team {
  0% {
    transform: translateY(0) rotateX(0) scale(1);
    transform-origin: 50% 0%; }
  50% {
    transform: translateY(-50%) rotateX(-90deg) scale(0.2);
    transform-origin: 50% 50%; }
  100% {
    transform: translateY(-100%) rotateX(-180deg) scale(1);
    transform-origin: 50% 100%; } }

/*------------Team-members-----------*/
.team-member {
  display: flex;
  align-content: center;
  justify-content: center;
  padding: 20px 0;
  text-align: left; }
  .team-member .member-photo {
    height: 100%; }
    .team-member .member-photo > img {
      border-radius: 50%; }
  .team-member .member-label {
    color: #8c9597; }
  .team-member .member-name {
    color: #5e5e5e;
    font-weight: 700;
    font-size: 30px; }
  .team-member .member-function {
    font-size: 20px; }
    .team-member .member-function.member-0 {
      color: #00e655; }
    .team-member .member-function.member-1 {
      color: #0091e6; }
    .team-member .member-function.member-2 {
      color: #e65500; }
    .team-member .member-function.member-3 {
      color: #e60091; }
    .team-member .member-function.member-4 {
      color: #00e6c8; }
    .team-member .member-function.member-5 {
      color: #5500e6; }
  .team-member .member-socials > img {
    width: 30px;
    height: 30px;
    margin: 0 5px; }

.load-more-button {
  background-color: rgba(0, 0, 0, 0.2);
  border: 4px solid white;
  border-radius: 3px;
  height: 70px;
  padding: 0 50px;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin: 20px;
  font-weight: 700;
  transition: 0.4s;
  font-size: 18px; }
  .load-more-button:hover {
    background-color: rgba(37, 37, 37, 0.2); }
